import _ from 'lodash'
import React, { Component } from 'react'
import {
    Container,
    Dropdown,
    Header,
    Icon,
    Image,
    Menu,
    Visibility,
    Popup
} from 'semantic-ui-react'

const menuStyle = {
    border: 'none',
    borderRadius: 0,
    boxShadow: 'none',
    marginBottom: '1em',
    marginTop: '4em',
    transition: 'box-shadow 0.5s ease, padding 0.5s ease',
}

const fixedMenuStyle = {
    backgroundColor: '#fff',
    border: '1px solid #ddd',
    boxShadow: '0px 3px 5px rgba(0, 0, 0, 0.2)',
}

const overlayStyle = {
    float: 'left',
    margin: '0em 3em 1em 0em',
}

const fixedOverlayStyle = {
    ...overlayStyle,
    position: 'fixed',
    top: '80px',
    zIndex: 10,
}

const overlayMenuStyle = {
    position: 'relative',
    left: 0,
    transition: 'left 0.5s ease',
}

const fixedOverlayMenuStyle = {
    ...overlayMenuStyle,
    left: '800px',
}

const Paragraph = () => (
    <div>
        <p>Murat YILMAZ Hakkında...</p>
        <p>Trabzon Maçka'da Doğdum.</p>
        <p>Karadeniz Ereğli'de Büyüdüm.</p>
        <p>Yıldız Teknik Üniversitesi Elektronik ve Haberleşme Mühendisliği Mezunuyum.</p>
        <p>Kod Yazmayı, Yazılım Süreçlerini, Yeni Yazılım Teknolojilerini Öğrenmeyi, Uygulamayı Çok Severim. </p>
        <p>Öğrendiğim veya Öğrenip Uygulamadığım Teknolojiler İle İlgili Detayları Yukarıdaki Makale Kısmına Yazıyor olacağım.  </p>
        <p>Bana Ulaşmak İçin Sol Tarafı Kullanabilirsiniz.</p>
    </div>
);

const paddingLeft = {paddingLeft: '120px'};

export default class DefaultPage extends Component {
    state = {
        menuFixed: false,
        overlayFixed: false,
    }

    handleOverlayRef = (c) => {
        const { overlayRect } = this.state

        if (!overlayRect) {
            this.setState({ overlayRect: _.pick(c.getBoundingClientRect(), 'height', 'width') })
        }
    }

    stickOverlay = () => this.setState({ overlayFixed: true })

    stickTopMenu = () => this.setState({ menuFixed: true })

    unStickOverlay = () => this.setState({ overlayFixed: false })

    unStickTopMenu = () => this.setState({ menuFixed: false })

    render() {
        const { menuFixed, overlayFixed, overlayRect } = this.state

        return (
            <div>
                <style>
                    {`
          html, body {
            background: #fff;
          }
        `}
                </style>

                <Container text style={{ marginTop: '2em' }}>
                    <Header as='h1' style={paddingLeft}>Murat YILMAZ</Header>
                    <p style={paddingLeft}>
                        Yazılım Zanaatkârı
                    </p>
                </Container>

                <Visibility
                    onBottomPassed={this.stickTopMenu}
                    onBottomVisible={this.unStickTopMenu}
                    once={false}
                >
                    <Menu
                        borderless
                        fixed={menuFixed ? 'top' : undefined}
                        style={menuFixed ? fixedMenuStyle : menuStyle}
                    >
                        <Container text>
                            <Menu.Menu position='right'>
                                <Dropdown text='Makaleler' pointing className='link item'>
                                    <Dropdown.Menu>
                                        {/*<Dropdown.Item>List Item</Dropdown.Item>*/}
                                        {/*<Dropdown.Item>List Item</Dropdown.Item>*/}
                                        {/*<Dropdown.Divider />*/}
                                        {/*<Dropdown.Header>Header Item</Dropdown.Header>*/}
                                        {/*<Dropdown.Item>*/}
                                        {/*    <i className='dropdown icon' />*/}
                                        {/*    <span className='text'>Submenu</span>*/}
                                        {/*    <Dropdown.Menu>*/}
                                        {/*        <Dropdown.Item>List Item</Dropdown.Item>*/}
                                        {/*        <Dropdown.Item>List Item</Dropdown.Item>*/}
                                        {/*    </Dropdown.Menu>*/}
                                        {/*</Dropdown.Item>*/}
                                        {/*<Dropdown.Item>List Item</Dropdown.Item>*/}
                                    </Dropdown.Menu>
                                </Dropdown>
                            </Menu.Menu>
                        </Container>
                    </Menu>
                </Visibility>

                <Container text>
                    <Visibility
                        offset={80}
                        once={false}
                        onTopPassed={this.stickOverlay}
                        onTopVisible={this.unStickOverlay}
                        style={overlayFixed ? { ...overlayStyle, ...overlayRect } : {}}
                    />

                    <div ref={this.handleOverlayRef} style={overlayFixed ? fixedOverlayStyle : overlayStyle}>
                        <Menu
                            icon='labeled'
                            style={overlayFixed ? fixedOverlayMenuStyle : overlayMenuStyle}
                            vertical
                        >
                            <Menu.Item>
                                <Popup content='www.twitter.com/yimu187' trigger={
                                    <Icon name='twitter'
                                          link
                                          onClick={() => {window.open("https://www.twitter.com/yimu187")}} />
                                    } />
                                Twitter
                            </Menu.Item>

                            <Menu.Item>
                                <Popup content='www.instagram.com/yimu187' trigger={
                                    <Icon name='instagram'
                                          link
                                          onClick={() => {window.open("https://www.instagram.com/yimu187")}} />
                                } />
                                Instagram
                            </Menu.Item>

                            <Menu.Item>
                                <Popup content='www.github.com/yimu187' trigger={
                                    <Icon name='github'
                                          link
                                          onClick={() => {window.open("https://www.github.com/yimu187")}} />
                                } />
                                GitHub
                            </Menu.Item>

                            <Menu.Item href={"mailto:yimu187@gmail.com"}>
                                <Popup content='yimu187@gmail.com' trigger={
                                    <Icon name='mail'
                                          link
                                    />
                                } />
                                Email
                            </Menu.Item>
                        </Menu>
                    </div>
                    <Paragraph />
                </Container>
            </div>
        )
    }
}
